import React from "react";
import { Helmet } from "react-helmet";

import "config/themes/_roulette.scss";
import SeedProvider from "providers/Seed";

import Page from "./page";

const RouletteWrapper = () => {
  return (
    <>
      <Helmet>
        <title>Roulette | Crypto Gambling Casino</title>
        <meta name="title" content="Play Bitcoin Roulette - Pasino.com" />
        <meta
          name="description"
          content="Spin the wheel and let the ball bounce. roulette on Pasino is a must play for every crypto gambler!  "
        />
        <meta
          name="keywords"
          content="Roulette Bitcoin, Roulette Pasino, Roulette Litecoin, Play Roulette,  "
        />
        <meta name="robots" content="index, follow" />
        <meta http-equiv="Content-Type" content="text/html; charset=utf-8" />
        <meta name="language" content="English" />
      </Helmet>
      <SeedProvider gameName="roulette">
        <Page />
      </SeedProvider>
    </>
  );
};

export default RouletteWrapper;
